body {
  background: url(./images/aleksandr-popov-JhYnL-BiP18-unsplash.jpg);
  background-position: center;
  background-size: cover;
  font-size: 18px;
  text-align: center;
}

.banner {
  color: white;
  text-align: center;
  width: 100%;
  height: 15vh;
  background-color: #42449C;
  position: fixed;
  top: 0;
  border: 2px solid black;
}

.banner h1 {
  font-size: 3rem;
  margin-bottom: 0;
  -webkit-text-stroke-color: black;
  -webkit-text-stroke-width: 2px;
}

.banner h2 {
  font-size: 1.5rem;
  margin-top: 0;
  font-style: italic;
}

.green {
  color: rgb(12, 194, 12);
}

.double-column {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 17vh;
  height: 100%;
}

.column-div {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: rgba(66, 68, 156, 0.72);
  margin: 0 2rem;
  border-radius: 15px;
  border: 2px solid black;
  height: 100%;
  box-shadow: 4px 4px black;
}

ul {
  list-style: none;
  border-block-start: 1px solid grey;
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0;
}

li {
  width: 100%;
  border-block-end: 1px solid grey;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.hoverScale {
  transition: all 0.25s;
}

.hoverScale:hover, .hoverScale:active {
  transform: scale(1.1);
}

button {
  border-radius: 15px;
  margin-left: 0.25rem;
  font-size: 1.15rem;
  color: white;
  background-color: #42449C;
  border: 2px solid black;
  box-shadow: 4px 4px black;
}

input {
  border-radius: 15px;
  box-shadow: 4px 4px black;
}

p {
  font-size: 1.5rem;
  font-weight: bold;
}

form {
  background-color: #110f24;
  padding: 0.5rem;
  border-radius: 15px;
  margin: 0.5rem 0;
  display: flex;
  align-items: center;
  margin: 1rem 1rem;
}

@media screen and (max-width: 750px) {
  .double-column {
    flex-direction: column;
    align-items: center;
  }

  .column-div {
    width: 90%;
    margin: .75rem;
  }

  .banner h1 {
    margin-top: 0;
  }
}

img {
  width: 250px;
  height: 250px;
  margin: .5rem 0;
}